      <div class="title">
        <h2>Статьи, которые очень интересные</h2>
      </div>
      <div class="slider row align-content-center">
        <div class="col-1">
          <button class="h-100" type="button" v-on:click="previousSlide">
            <img src="@/assets/left-arrow.png" height="100" width="71"/>
          </button>
        </div>
        <div class="col-10">
          <div class="row row-cols-3">
            <div v-for="article in computedArticles" :key="article">
              <div class="card p-4">
                <a class="overflow-hidden" v-bind:href="article.link">
                  <h4>{{article.title}}</h4>
                  <h6>{{article.description}}</h6>
                </a>
              </div>
            </div>
          </div>
        </div>
