          link: "http://google.com",
        },
      ],
    }
  },
  computed: {
    computedArticles() {
      if(this.counter <  this.articles.length - 3) {
        return this.articles.slice(this.counter, this.counter + 3);
      } else {
        return this.articles.slice(this.articles.length - 3, this.articles.length);
      }
    }
  },
  methods: {
    nextSlide() {
      if(this.counter < this.articles.length - 3) {
        this.counter ++;
      } else {
        this.counter = 0;
      }
      console.log(`counter: ${this.counter}`);
    },

    previousSlide() {
      if(this.counter > 0) {
        this.counter --;
      } else {
        this.counter = this.articles.length - 3;
      }
      console.log(`counter: ${this.counter}`);
    }
  }
}
</script>

